import React, { Component } from 'react';
import { Nav, Navbar, NavbarBrand, NavbarToggler, Collapse, NavItem,
         Jumbotron, Button, Modal, ModalHeader,
          ModalBody, Form, FormGroup, Label, Input } from 'reactstrap';
import { NavLink } from 'react-router-dom';

class Header extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isNavOpen: false,
      isModalOpen: false
    };

    this.toggleNav = this.toggleNav.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
    this.handleLogin = this.handleLogin.bind(this)

  }

  toggleNav() {
    this.setState({
      isNavOpen: !this.state.isNavOpen
    });
  }

  toggleModal() {
    this.setState({
      isModalOpen: !this.state.isModalOpen
  })
}

  handleLogin(event) {
    this.toggleModal();
    alert("Username: " + this.username.value + " Password: " + this.password.value
        + " Remember: " + this.remember.checked);
    event.preventDefault();

  }

  render() {
    return(
    <div>
      <Navbar dark expand="md">
        <div className="container">
          <NavbarToggler onClick={this.toggleNav} />
          <NavbarBrand className="mr-auto" href="/"><img src='assets/images/logo-dudis.png' height="30" width="41" alt='DudisToys' /></NavbarBrand>
          <Collapse isOpen={this.state.isNavOpen} navbar>
              <Nav navbar>
              <NavItem>
                  <NavLink className="nav-link"  to='/home'><span className="fa fa-home fa-lg"></span> Home</NavLink>
              </NavItem>
              <NavItem>
                  <NavLink className="nav-link" to='/aboutus'><span className="fa fa-info fa-lg"></span> Nosotros</NavLink>
              </NavItem>
              <NavItem>
                  <NavLink className="nav-link"  to='/menu'><span className="fa fa-list fa-lg"></span> Galer칤a</NavLink>
              </NavItem>
              <NavItem>
                  <NavLink className="nav-link" to='/contactus'><span className="fa fa-address-card fa-lg"></span> Cont치ctanos</NavLink>
              </NavItem>
              </Nav>
              <Nav className="ml-auto" navbar>
                <NavItem>
                  <Button outline onClick={this.toggleModal}>
                    <span className="fa fa-sign-in fa-lg"></span> Login
                  </Button>
                </NavItem>
              </Nav>
          </Collapse>
        </div>
      </Navbar>
      <Jumbotron>
           <div className="container">
               <div className="row row-header">
                   <div className="col-12 col-sm-6">
                       <h1>Dudis'Toys</h1>
                       <p>Bienvenidos a esta fascinante p치gina de tecnolog칤a y entretenimiento con un esp칤ritu innovador y m치gico hacia la vida. <span role="img" aria-label="">游</span></p>
                   </div>
               </div>
           </div>
       </Jumbotron>
       <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
         <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
         <ModalBody>
           <Form onSubmit={this.handleLogin}>
             <FormGroup>
               <Label htmlFor="username">Username</Label>
               <Input type="text" id="username" name="username" innerRef={(input) => this.username = input} />
             </FormGroup>
             <FormGroup>
               <Label htmlFor="password">Password</Label>
               <Input type="password" id="password" name="password" innerRef={(input) => this.password = input} />
             </FormGroup>
             <FormGroup check>
               <Label check>
                 <Input type="checkbox" name="remember" innerRef={(input) => this.remember = input} /> Remember me
               </Label>
             </FormGroup>
             <Button type="submit" value="submit" color="primary">Login</Button>
           </Form>
         </ModalBody>
       </Modal>
    </div>
    );
  }
}

export default Header;